.hero {
	display: flex;
	align-items: center;
	height: 100vh;
	width: 100vw;
	background-color: skyblue;
	background-size: cover;
	background-position: center;
	position: relative;
	overflow: hidden;
	font-family: 'Gill Sans', sans-serif;
}

.loginBody {
	margin: 0;
	height: 100vh;
	width: 100vw;
	overflow: hidden;
}

.login {
	box-sizing: border-box;
	display: grid;

	grid-template-columns: 1fr;
	justify-items: center;
	align-items: center;
	margin: 0 auto;
	margin-top: 10px;
	width: 500px;
	height: 550px;
	color: rgb(44, 43, 43);
	border-radius: 40px;
	padding: 60px 35px 35px 35px;
	background-color: skyblue;
	box-shadow: 13px 13px 20px rgb(191, 232, 248),
		-13px -13px 20px rgb(140, 195, 216);
}
.logo img {
	width: 100px;
	height: 100px;
	border-radius: 50%;
	margin: 0 auto;
	box-shadow: 0px 0px 2px #5f5f5f, 0px 0px 0px 5px #ecf0f3, 8px 8px 15px #a7aaaf,
		-8px -8px 15px #fff;
}

.title {
	text-align: center;
	font-size: 2rem;
	padding-top: 2vh;
	letter-spacing: 0.5px;
}
.subtitle {
	text-align: center;
	font-size: 1rem;
	padding-top: 8px;
	letter-spacing: 3px;
}

.fields {
	width: 100%;
}

.fields input {
	border: none;
	outline: none;
	background: none;
	font-size: 18px;
	color: #555;
	padding: 20px 10px 20px 5px;
}
.email,
.password {
	margin-bottom: 30px;
	border-radius: 25px;
	box-shadow: inset 5px 5px 10px #1c87c9,
		inset 10px 10px 10px rgb(204, 204, 204);
}

.fields svg {
	height: 22px;
	margin: 0 10px -3px 25px;
}

.signinBtn {
	outline: none;
	border: none;
	cursor: pointer;
	width: 60%;
	height: 40px;
	border-radius: 30px;
	font-size: 1.5rem;
	font-weight: 700;
	font-family: 'Lato', sans-serif;
	color: #fff;
	margin-bottom: 200px;
	text-align: center;
	background-color: skyblue;
	box-shadow: 5px 5px 10px #1c87c9, 10px 10px 10px rgb(204, 204, 204);
}

.highway {
	height: 100px;
	width: 1000%;
	display: block;
	background-image: url(../../images/car-animation/road.jpg);
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 1;
	background-repeat: repeat-x;
	animation: highway 5s linear infinite;
}

.city {
	height: 170px;
	width: 500%;
	background-image: url(../../images/car-animation/city.png);
	position: absolute;
	bottom: 100px;
	left: 0;
	right: 0;
	display: block;
	z-index: 1;
	background-repeat: repeat-x;
	animation: city 5s linear infinite;
}

.car {
	width: 400px;
	left: 50%;
	bottom: 5px;
	transform: translateX(-50%);
	position: absolute;
	z-index: 1;
}

.car img {
	width: 100%;
	animation: car 1s linear infinite;
}

.wheel {
	left: 50%;
	bottom: 85px;
	transform: translateX(-50%);
	position: absolute;
	z-index: 2;
}

.wheel img {
	width: 72px;
	height: 72px;
	animation: wheel 1s linear infinite;
}

.backWheel {
	left: -165px;
	position: absolute;
}

.frontWheel {
	left: 80px;
	position: absolute;
}

@keyframes highway {
	100% {
		transform: translateX(-3400px);
	}
}

@keyframes city {
	100% {
		transform: translateX(-1400px);
	}
}

@keyframes car {
	100% {
		transform: translateY(-1px);
	}
	50% {
		transform: translateY(1px);
	}
	0% {
		transform: translateY(-1px);
	}
}

@keyframes wheel {
	100% {
		transform: rotate(360deg);
	}
}

@media only screen and (max-width: 600px) {
	.highway {
		display: none;
	}
	.city {
		display: none;
	}
	.car {
		display: none;
	}

	.car img {
		width: 50%;
		animation: car 1s linear infinite;
	}

	.wheel {
		display: none;
	}

	.wheel img {
		display: none;
	}

	.login {
		width: 100vw;
		height: 100vh;
	}
}
